<script>
import { getData } from "common/utils/public_function/commonUtils";
export default {
  data() {
    return {
      authority: {},
      radio: "",
      TableData: {
        tableData: []
      },
      handlerArr: [],
      params: {},
      per_page: [30, 50, 100, 500],
      loading: true,
      searchParams: {},
      goPage: 1, //
      pageSize: 30,
      timeValue: [],
      dialogmultipleSelection: [], //
      goodsList: [],
      options4: [],
      list: [],
      addmultipleSelection: [],
      addmultipleSelectionBottom: [],
      loadSign: true,
      loadingStatus:false
    };
  },
  props: ["message", "companyID"],
  methods: {
    handleSelectionChange(val) {
      this.addmultipleSelection = val;
    },
    handleSelectionChangeBottom(val) {
      this.addmultipleSelectionBottom = val;
    },
    topScroll(e) {
      console.log("scroll", event.target.scrollLeft);
    },
    bottomScroll(e) {
      console.log("scroll", event.target.scrollLeft);
    },
    loadMore() {
      //    bug
      // if (this.loadSign) {
      //   this.loadSign = false;
      //   if (localStorage.getItem("page") != null) {
      //     this.goPage = JSON.parse(localStorage.getItem("page")).page + 1;
      //   } else {
      //     this.goPage = 1;
      //   }
      //   Object.assign(this.searchParams.params, {
      //     page: this.goPage
      //   });
      //   getData(this.searchParams, 4, this.unique);
      // }
    },
    unique() {
      var hash = {}; //arr
      this.TableData.tableData = this.TableData.tableData.reduce(function(
        item,
        next
      ) {
        hash[next.company_appraisal_goods_id]
          ? ""
          : (hash[next.company_appraisal_goods_id] = true && item.push(next));
        return item;
      },
      []);
      let page = { page: this.goPage };
      localStorage.setItem("page", JSON.stringify(page));
      this.loadSign = true;
      this.loadingStatus = false;
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams);
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      getData(this.searchParams);
    },
    searchF() {
      this.onSearch();
    },
    onReset() {
      /**   */
      this.$set(this, "params", {});
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: {
          supplier_id: this.message,
          company_id: this.companyID
        },
        TableData: this.TableData, //
        API: "getCompanyAppraisalListPublics", //
        vm: this //this
      });
      getData(this.searchParams);
    },
    dialoghandleSelectionChange(val) {
      this.dialogmultipleSelection = val;
    },
    toDownList(row) {
      let dataArr = this.TableData.tableData;
      let arr = [];
      if (!this.handlerArr.length) {
        this.handlerArr.unshift(row);
      } else {
        for (let i = 0; i < this.handlerArr.length; i++) {
          arr.push(this.handlerArr[i].company_appraisal_goods_id);
        }
        if (arr.includes(row.company_appraisal_goods_id)) {
          this.$message.warning("");
          return false;
        } else {
          this.handlerArr.unshift(row);
        }
      }
      dataArr.map((item, index) => {
        if (row.company_appraisal_goods_id == item.company_appraisal_goods_id) {
          dataArr.splice(index, 1);
        }
      });
    },
    toUpList(row) {
      let dataArr = this.handlerArr;
      let arr = [];
      if (!this.TableData.tableData.length) {
        this.TableData.tableData.unshift(row);
      } else {
        for (let i = 0; i < this.TableData.tableData.length; i++) {
          arr.push(this.TableData.tableData[i].company_appraisal_goods_id);
        }
        if (arr.includes(row.company_appraisal_goods_id)) {
          this.$message.warning("");
          return false;
        } else {
          this.TableData.tableData.unshift(row);
        }
      }
      dataArr.map((item, index) => {
        if (row.company_appraisal_goods_id == item.company_appraisal_goods_id) {
          dataArr.splice(index, 1);
        }
      });
    },
    downToList() {
      console.log(this.TableData.tableData)
      if (!this.addmultipleSelection.length) {
        this.$message.warning("");
        return false;
      }
      this.handlerArr = [...this.addmultipleSelection, ...this.handlerArr];
      this.addmultipleSelectionBottom = this.handlerArr;
      let dataArr = this.TableData.tableData;
      let checkedArr = this.addmultipleSelection;
      var hash = {}; //arr
      this.handlerArr = this.handlerArr.reduce(function(item, next) {
        hash[next.company_appraisal_goods_id]
          ? ""
          : (hash[next.company_appraisal_goods_id] = true && item.push(next));
        return item;
      }, []);
      for (let i = 0; i < checkedArr.length; i++) {
        for (let b = dataArr.length - 1; b >= 0; b--) {
          if (
            checkedArr[i].company_appraisal_goods_id ==
            dataArr[b].company_appraisal_goods_id
          ) {
            dataArr.splice(b, 1);
          }
        }
      }
    },
    upToList() {
      if (!this.addmultipleSelectionBottom.length) {
        this.$message.warning("");
        return false;
      }
      this.TableData.tableData = [
        ...this.addmultipleSelectionBottom,
        ...this.TableData.tableData
      ];
      let dataArr = this.handlerArr;
      let checkedArr = this.addmultipleSelectionBottom;
      var hash = {}; //arr
      this.TableData.tableData = this.TableData.tableData.reduce(function(
        item,
        next
      ) {
        hash[next.company_appraisal_goods_id]
          ? ""
          : (hash[next.company_appraisal_goods_id] = true && item.push(next));
        return item;
      },
      []);
      for (let i = 0; i < checkedArr.length; i++) {
        for (let b = dataArr.length - 1; b >= 0; b--) {
          if (
            checkedArr[i].company_appraisal_goods_id ==
            dataArr[b].company_appraisal_goods_id
          ) {
            dataArr.splice(b, 1);
          }
        }
      }
    },
    closeDialog() {
      // 
      this.$parent.salesDocumentsVisible = false;
      localStorage.removeItem("page");
    },
    dialogSubmitForm() {
      //
      this.$emit("getmessage", this.handlerArr);
      this.$parent.salesDocumentsVisible = false;
    }
  },
  watch: {
    TableData: {
      handler() {
        for (let i = this.TableData.tableData.length - 1; i >= 0; i--) {
          for (let a = 0; a < this.handlerArr.length; a++) {
            if (
              this.TableData.tableData[i].company_appraisal_goods_id ==
              this.handlerArr[a].company_appraisal_goods_id
            ) {
              this.TableData.tableData.splice(i, 1);
            }
          }
        }
      },
      deep: true
    }
  },
  mounted() {
    this.$set(this, "searchParams", {
      params: {
        supplier_id: this.message,
        company_id: this.companyID
      }, //1
      TableData: this.TableData, //
      API: "getCompanyAppraisalListPublics", //
      vm: this //this
    });
    getData(this.searchParams); //
  }
};
</script>

