<template>
  <div>
    <div class="search_container">
      <el-form :inline="true" ref="addFormInline" :model="params" class="demo-form-inline">
        <el-form-item label="" size="mini" :rules="rules.company_id" prop="company_id">
          <el-select clearable v-model="params.company_id" placeholder="" @focus="companyListInvok" @change="companyChange" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1?'optionStyle':''">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="()" :rules="rules.supplier_id" size="mini" prop="supplier_id">
          <el-select clearable v-model="params.supplier_id" filterable @focus="supplierListInvok" :filter-method="supplierListInvok">
            <el-option v-for="item in supplierList" :label="item.supplier_name" :value="item.supplier_id" :key="item.supplier_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" size="mini">
          <el-date-picker v-model="params.business_time" type="date" placeholder="" value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="" size="mini">
          {{(function(){if(params.department_id == 0)params.department_id = null})()}}
          <el-select clearable v-model="params.department_id" @focus="DepartmentListInvok" placeholder="" filterable>
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" size="mini">
          <el-input v-model="params.remark" type="text" maxlength="300"></el-input>
        </el-form-item>

      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="quoteOrder" class="addBtn" size="mini"></el-button>
      <el-button plain icon="el-icon-delete" @click="deletes" size="mini"></el-button>
    </div>
    <div class="table_container">
      <!--  -->
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="getMessage" style="width: 100%" max-height="600" @selection-change="handleSelectionChange">
        <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="brand_name" label="">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" label="">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" label="" min-width="500">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="tire_un" label="">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="symptom_name" width="200" label="">
          <template slot-scope="scope">
            <el-select class="jz" clearable v-model="scope.row.symptom_id" multiple collapse-tags placeholder="">
              <el-option :label="item.symptom_name" :value="item.symptom_id" :key="item.symptom_id" v-for="item in diseaseList"></el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="company_status" label="" width="120">
          <template slot-scope="scope">
            <span v-if="scope.row.company_status==1"></span>
            <span v-if="scope.row.company_status==2"></span>
            <span v-if="scope.row.company_status==3"></span>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="raw_flowerdeep" label="" width="120">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="surplus_flowerdeep" label="" width="120">
          <template slot-scope="scope">
            <el-input v-model="scope.row.surplus_flowerdeep" placeholder="" suffix-icon="el-icon-edit-outline" @focus="changeStatusF(true)"></el-input>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="factory_flowerdeep" label="" width="120">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="charge_flowerdeep" label="" width="120">
          <template slot-scope="scope">
            {{scope.row.charge_flowerdeep|priceFormat}}
          </template>
          <!-- <template slot-scope="scope">
            <span>{{(function(){
              scope.row.charge_flowerdeep=isNumber(scope.row.raw_flowerdeep)-isNumber(scope.row.surplus_flowerdeep)-isNumber(scope.row.factory_flowerdeep);
              return scope.row.charge_flowerdeep })()}}</span>
          </template> -->
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="mm_wearprice" label="" width="120">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="wear_price" label="" width="120">
          <template slot-scope="scope">
            <el-input v-model="scope.row.wear_price" placeholder="" suffix-icon="el-icon-edit-outline" @focus="changeStatusF(false)"></el-input>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="intube_price" label="" width="120">
          <template slot-scope="scope">
            <el-input v-model="scope.row.intube_price" placeholder="" suffix-icon="el-icon-edit-outline" @input.native="intubePrice(scope.row,scope.$index)" @focus="changeStatusF(false)"></el-input>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="flap_price" label="" width="120">
          <template slot-scope="scope">
            <el-input v-model="scope.row.flap_price" placeholder="" suffix-icon="el-icon-edit-outline" @input.native="flapPrice(scope.row,scope.$index)" @focus="changeStatusF(false)"></el-input>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="inflap_price" label="" width="130">
          <template slot-scope="scope">
            <el-input v-model="scope.row.inflap_price" placeholder="" suffix-icon="el-icon-edit-outline" @focus="changeStatusF(false)"></el-input>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="purchase_price" label="" width="130">
          <template slot-scope="scope">
            <el-input v-model="scope.row.purchase_price" placeholder="" suffix-icon="el-icon-edit-outline" @focus="changeStatusF(false)"></el-input>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="factory_price" label="" width="120">
          <!-- <template slot-scope="scope">
            <span>{{(function(){
              scope.row.factory_price=isNumber(scope.row.goods_price)-isNumber(scope.row.wear_price)-isNumber(scope.row.inflap_price);
              return scope.row.factory_price })()}}</span>
          </template> -->
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="factory_status" label="" width="160">
          <template slot-scope="scope">
            {{(function(){if(!scope.row.factory_status){$set(getMessage[scope.$index],'factory_status',1) } })()}}
            <el-select clearable v-model="scope.row.factory_status" placeholder="">
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="2"></el-option>
            </el-select>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack"> </el-button>
      <el-button type="primary" @click="submit('addFormInline')"> </el-button>
    </div>
    <appraisal-dalog :message="params.supplier_id" :companyID="params.company_id" @getmessage="dongList" v-if="salesDocumentsVisible"></appraisal-dalog>
  </div>
</template>

<script>
import appraisalDalog from "@/components/element/aftersale/share/appraisalOrder_dalog";
import { mapActions, mapGetters } from "vuex";
import { getData, opthionList } from "common/utils/public_function/commonUtils";
import { setTimeout } from "timers";
export default {
  name: "aftersaleFactoryjudgementAdd",
  data() {
    return {
      authority: {},
      params: {},
      addmultipleSelection: [],
      salesDocumentsVisible: false, //
      return_allocation_arr: {}, //
      rowIndex: "",
      value3: "",
      getMessage: [],
      getMessage2: [],
      allocationList: [],
      options4: [],
      list: [],
      loading: false,
      getwarehouseOption: true,
      supplier_appraisal_id: "",
      isWearPrice: true,
      isEditFirst: false
    };
  },
  components: {
    appraisalDalog //
  },
  computed: {
    ...mapGetters(["factoryjudgementAdd_Get", "factoryjudgementDefault_Get"])
  },
  methods: {
    ...mapActions(["factoryjudgementDefault"]),
    writeS(scope, index) {
      this.$set(
        this.getMessage[index],
        "wear_price",
        Number(scope.charge_flowerdeep * scope.mm_wearprice).toFixed(2) != "NaN"
          ? Number(scope.charge_flowerdeep * scope.mm_wearprice).toFixed(2)
          : 0
      );
    },
    //
    changeStatusF(boolean) {
      this.isWearPrice = boolean;
    },
    intubePrice(row, index, e) {
      this.$set(
        row,
        "inflap_price",
        Number(
          Number(this.getMessage[index].intube_price) +
            Number(this.getMessage[index].flap_price)
        ).toFixed(2) != "NaN"
          ? Number(
              Number(this.getMessage[index].intube_price) +
                Number(this.getMessage[index].flap_price)
            ).toFixed(2)
          : 0
      );
    },
    flapPrice(row, index, e) {
      this.$set(
        row,
        "inflap_price",
        Number(
          Number(this.getMessage[index].intube_price) +
            Number(this.getMessage[index].flap_price)
        ).toFixed(2) != "NaN"
          ? Number(
              Number(this.getMessage[index].intube_price) +
                Number(this.getMessage[index].flap_price)
            ).toFixed(2)
          : 0
      );
    },
    writeText(val, index) {
      this.$set(this.getMessage[index], "wear_price", val);
    },
    changeGetgetwarehouseOption(val) {},
    isNumber(val) {
      return val ? Number(val) : 0;
    },
    dongList(val) {
      if (val.length > 0) {
        let company_appraisal_goods_idArr = this.getMessage.map(item => {
          return item.company_appraisal_goods_id;
        });
        //
        // val.map(item => {
        //   item.inflap_price = "";
        // });
        //
        // val.map(item => {
        //   item.inflap_price =
        //     Number(Number(item.intube_price) + Number(item.flap_price)).toFixed(
        //       2
        //     ) != "NaN"
        //       ? Number(
        //           Number(item.intube_price) + Number(item.flap_price)
        //         ).toFixed(2)
        //       : 0;

        //   item.factory_price =
        //     this.isNumber(item.purchase_price) -
        //     this.isNumber(item.wear_price) -
        //     this.isNumber(item.inflap_price);

        //   item.charge_flowerdeep,
        //     this.isNumber(item.raw_flowerdeep) -
        //       this.isNumber(item.surplus_flowerdeep) -
        //       this.isNumber(item.factory_flowerdeep);

        //   item.wear_price =
        //     this.isNumber(item.charge_flowerdeep) *
        //     this.isNumber(item.mm_wearprice);
        // });
        val.forEach((item, i) => {
          if (
            !company_appraisal_goods_idArr.includes(
              item.company_appraisal_goods_id
            )
          ) {
            let s = [];
            String(item.symptom_id)
              .split(",")
              .forEach(item2 => {
                s.push(Number(item2));
              });
            item.symptom_id = s;
            this.getMessage.push(item);
          } else {
            this.$message({
              message: "" + item.tire_un + "",
              type: "error"
            });
          }
        });
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    deletes() {
      if (this.multipleSelection.length) {
        let a = new Set(this.getMessage);
        let b = new Set(this.multipleSelection);
        let differenceABSet = new Set([...a].filter(x => !b.has(x)));
        this.getMessage = Array.from(differenceABSet);
      } else {
        this.$message({
          message: "",
          type: "warning"
        });
      }
    },
    companyChange(val) {
      // getDepartmentListCommon(this, this.params);
      this.$set(this, "getMessage", []);
      this.$set(this.params, "department_id", ""); // 
      this.$set(this.params, "supplier_id", ""); // 
    },
    goBack() {
      // wz annotation cache
      // this.factoryjudgementDefault({
      //   // moduleName: "factoryjudgementDefault",
      //   // params: this.factoryjudgementAdd_Get.params,
      //   params: this.factoryjudgementDefault_Get ? this.factoryjudgementDefault_Get.params : {},
      //   goPage: this.factoryjudgementAdd_Get.goPage,
      //   pageSize: this.factoryjudgementAdd_Get.pageSize
      // });
      this.$router.push({
        path: "/aftersale/factoryjudgement"
      });
    },
    quoteOrder() {
      if (!this.params.supplier_id) {
        this.$message({
          message: "()",
          type: "warning"
        });
      } else {
        this.salesDocumentsVisible = true;
      }
      //
    },
    formatterCharge(row, column, cellValue, index) {
      row.charge_flowerdeep =
        this.isNumber(row.raw_flowerdeep) -
        this.isNumber(row.surplus_flowerdeep) -
        this.isNumber(row.factory_flowerdeep);
      return row.charge_flowerdeep;
    },
    formatterInflap(row, column, cellValue, index) {
      this.$set(
        this.getMessage[index],
        "inflap_price",
        Number(Number(row.intube_price) + Number(row.flap_price)).toFixed(2) !=
          "NaN"
          ? Number(Number(row.intube_price) + Number(row.flap_price)).toFixed(2)
          : 0
      );
      return row.inflap_price;
    },
    formatterFactory(row, column, cellValue, index) {
      row.factory_price =
        this.isNumber(row.goods_price) -
        this.isNumber(row.wear_price) -
        this.isNumber(row.inflap_price);
      return row.factory_price;
    },
    formatterStatus(row, column, cellValue, index) {
      if (!row.factory_status) {
        this.$set(this.getMessage[index], "factory_status", 1);
      }
    },
    submit() {
      if (!this.params.supplier_id) {
        this.$message({
          message: "()",
          type: "warning"
        });
        return false;
      }

      let tire_json = this.getMessage.map(item => {
        return {
          brand_id: item.brand_id,
          goods_id: item.goods_id,
          symptom_id: item.symptom_id.join(),
          company_status: item.company_status,
          tire_un: item.tire_un,
          supplier_id: item.supplier_id,
          raw_flowerdeep: item.raw_flowerdeep,
          surplus_flowerdeep: item.surplus_flowerdeep,
          factory_flowerdeep: item.factory_flowerdeep,
          charge_flowerdeep: item.charge_flowerdeep,
          mm_wearprice: item.mm_wearprice,
          wear_price: item.wear_price,
          inner_costprice: item.inner_costprice,
          flap_price: item.flap_price ? item.flap_price : 0,
          inflap_price: item.inflap_price ? item.inflap_price : 0,
          goods_price: item.purchase_price ? item.purchase_price : 0,
          //purchase_price: item.purchase_price ? item.purchase_price : 0,
          intube_price: item.intube_price ? item.intube_price : 0,
          factory_status: item.factory_status,
          // ==""?'1':'2'
          company_appraisal_goods_id: item.company_appraisal_goods_id
          //   factory_price:'88',
          //
          // compensation_price: "88"
        };
      });
      // console.log(tire_json);
      // this.params.business_time = this.$publicFution.formatData(
      //   new Date()
      // );
      let obj = {
        company_id: this.params.company_id,
        department_id: this.params.department_id,
        supplier_id: this.params.supplier_id,
        business_time: this.params.business_time,
        remark: this.params.remark == undefined ? "" : this.params.remark,
        goods_info: JSON.stringify(tire_json)
      };
      let editObj = {
        factory_appraisal_id: this.supplier_appraisal_id,
        company_id: this.params.company_id,
        department_id: this.params.department_id,
        supplier_id: this.params.supplier_id,
        business_time: this.params.business_time,
        remark: this.params.remark == undefined ? "" : this.params.remark,
        goods_info: JSON.stringify(tire_json)
      };
      let vm = this;
      if (this.supplier_appraisal_id != "") {
        vm.$axios({
          method: "post",
          url: vm.$api.exitCompanyAppraisal,
          data: editObj
        }).then(res => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.data,
              type: "success"
            });
            vm.goBack();
          }
        });
      } else {
        vm.$axios({
          method: "post",
          url: vm.$api.addSupplierAppraisal,
          data: obj
        }).then(res => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.data,
              type: "success"
            });
            vm.goBack();
          }
        });
      }
    }
  },
  watch: {
    getMessage: {
      handler() {
        if (!this.isEditFirst) {
          this.getMessage.map((item, index) => {
            this.$set(
              this.getMessage[index],
              "factory_price",
              (
                this.isNumber(item.purchase_price) -
                this.isNumber(item.wear_price) -
                this.isNumber(item.inflap_price)
              ).toFixed(2) < 0
                ? 0
                : (
                    this.isNumber(item.purchase_price) -
                    this.isNumber(item.wear_price) -
                    this.isNumber(item.inflap_price)
                  ).toFixed(2)
            );
            this.$set(
              this.getMessage[index],
              "charge_flowerdeep",
              (
                this.isNumber(item.raw_flowerdeep) -
                this.isNumber(item.surplus_flowerdeep) -
                this.isNumber(item.factory_flowerdeep)
              ).toFixed(2) < 0
                ? 0
                : (
                    this.isNumber(item.raw_flowerdeep) -
                    this.isNumber(item.surplus_flowerdeep) -
                    this.isNumber(item.factory_flowerdeep)
                  ).toFixed(2)
            );
            if (this.isWearPrice) {
              this.$set(
                this.getMessage[index],
                "wear_price",
                (
                  this.isNumber(item.charge_flowerdeep) *
                  this.isNumber(item.mm_wearprice)
                ).toFixed(2)
              );
            }
          });
        }
      },
      deep: true
    }
  },
  created() {
    let vm = this;
    // this.$axMethod.getDiseaseList().then(response => {
    //   this.symptomList.options = response.data.data;
    // });
    if (this.factoryjudgementAdd_Get.factory_appraisal_id != undefined) {
      this.isEditFirst = true;
      this.$axios({
        method: "get",
        url: this.$api.infoCompanyAppraisal,
        params: {
          factory_appraisal_id: this.factoryjudgementAdd_Get
            .factory_appraisal_id
        }
      }).then(({ data }) => {
        if (data.code == 200) {
          this.params = data.data.infoList;
          this.getMessage = data.data.goodsList;
          this.$nextTick(() => {
            this.isEditFirst = false;
          });
          // bindIdsF(this, data.data.infoList, [
          //   ["company_id", "company_name", "companyList"],
          //   ["department_id", "department_name", "departmentList", "options"],
          //   ["supplier_id", "supplier_name", "supplierList", "options"]
          // ]);
          opthionList(this, data.data.infoList);
          for (let i = 0; i < data.data.goodsList.length; i++) {
            let s = [];
            String(data.data.goodsList[i].symptom_id)
              .split(",")
              .forEach(val => {
                s.push(Number(val));
              });
            this.getMessage[i].symptom_id = s;

            if (data.data.goodsList[i].company_status == "") {
              this.getMessage[i].company_status = 1;
            } else if (data.data.goodsList[i].company_status == "") {
              this.getMessage[i].company_status = 2;
            } else if (data.data.goodsList[i].company_status == "") {
              this.getMessage[i].company_status = 3;
            }
          }
        }
      });
      this.supplier_appraisal_id = this.factoryjudgementAdd_Get.factory_appraisal_id;
    } else {
      // bindIdsF(this, localStorage, [
      //   ["company_id", "company_name", "companyList"],
      //   ["department_id", "department_name", "departmentList", "options"]
      // ]);
      opthionList(this, localStorage);
    }
  },
  mounted() {
    this.getNEwlist();
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$set(this, "params", {
      business_time: this.$publicFution.formatData(new Date()),
      company_id: Number(localStorage.company_id)
    });
    // this.getsymptomFoucs();
  }
};
</script>

<style lang="less" scoped>
.dialog-footer {
  margin-top: 40px;
  text-align: center;
}
</style>
